		jwk.p = hextob64u(keyObj.p.toString(16));
		jwk.q = hextob64u(keyObj.q.toString(16));
		jwk.dp = hextob64u(keyObj.dmp1.toString(16));
		jwk.dq = hextob64u(keyObj.dmq1.toString(16));
		jwk.qi = hextob64u(keyObj.coeff.toString(16));
